import React, { useEffect } from "react";
import { useStaticQuery, graphql, Link } from "gatsby";
import '../styles/estilo.css';
import '../styles/cursos.css';
import AOS from "aos";

/**
 * name: Nome do curso
 * img: caminho da imagem
 * description: descricao do curso
 * end: link para o curso ou para o ava
 */

const Cursos = (props) =>{
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  }, []);

  const data = useStaticQuery(query);
  return(
    <div className="card mb-3" data-aos="fade-up">
      <h3 className="nome-exp">{console.log(data.allStrapiCursos.edges[0].node.nome)}</h3>
      <div className="card-body">
        <p className="card-text">DDDDDDDEEEEEEESSSSSSSCCCCCCRRRRRRRRRIIIIIIPPPPTTTTIIIIIIIOOOOONNNNNNNNN</p>
        <div className="div-btn">
          <button type="button" className="btn btn-cursos" /*formaction={ props.end } */ Ir para curso></button>
        </div>
      </div> 
    </div>
  )
}

const query = graphql`
query {
  allStrapiCursos {
    edges {
      node {
        nome
        descricao
        EmAndamento
      }
    }
  }
}
`

export default Cursos;
